import {
    ASYNC_START,
    ASYNC_END,
    CHOOSE_PURCHASE,
    GET_PLANS,
  } from '../../constants/actionTypes';
    
  export default (state = {}, action) => {
    switch (action.type) {
      case CHOOSE_PURCHASE:
        return { ...state, choosenPlan:action.payload}
      
      case GET_PLANS:
        return { ...state, plans: action.payload}
      default:
        return state;
    }
    
    return state;
  };
    