import {
  GET_PRODUCTS,
  SET_PRODUCTS,
  GET_SHOP_DETAILS,
  GET_OUR_CHARGES,
} from '../../constants/actionTypes';

export default (state = {}, action) => {
  switch (action.type) {
    case GET_PRODUCTS:
      if (state.products) {
        return { ...state, products: action.pageNumber === 1 ? action.payload.products : [...state.products, ...action.payload.products] };
      }
      return { ...state, products: action.payload.products };
    case SET_PRODUCTS:
      return { ...state, products: action.payload.products };
    case GET_SHOP_DETAILS:
      return { ...state, shopDetails: action.payload };
    case GET_OUR_CHARGES:
      return { ...state, ourCharges: action.payload };
    default:
      return state;
  }
};
